
.mint-cell:first-child .mint-cell-wrapper{
  background-image: none !important;
}
.msgBody .help .mint-cell-title{
  padding-left: 18% !important;
  padding-bottom: 5px !important;
  width: 70%;
}
.helpBody .mint-header{
  background:transparent;
}
.msgBody .mint-cell-label{
  white-space:nowrap;
  overflow:hidden;
  text-overflow:ellipsis;
  width: 94%;
  padding: 2px 0;
}
.msgBody .mint-cell-wrapper{
  padding: 4px 0;
}
.help .mint-cell:last-child .mint-cell-title{
  padding-bottom: 13px !important
}
.time{
  position: absolute;
  display: block;
  top: 20px;
  right: 9%;
  font-size: 14px;
}
.loadmore{
  height: calc(100vh - 60px);
}
.msgBody .mint-badge{
  top: 0px;
  right: -6px;
  position: absolute;
}



