
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.shadow {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height:100%;
  width: 100%;
  background-color: rgba(0,0,0,0.4);
  z-index: 999;
}
.shadow:after {
   content: "";
   display: inline-block;
   height: 100%;
   width: 0;
   vertical-align: middle;
}
.logoutBox {
  display: inline-block;
  min-width: 60%;
  background-color: #fff;
  border-radius: 5px;
}
.logoutBox .header {
  padding: 15px 20px 0;
}
.logoutBox .title {
  font-size: 0.16rem;
  font-weight: 700;
  height: 18px;
  color: #333;
}
.logoutBox .content {
  padding: 15px 20px;
  font-size: 0.14rem;
}
.logoutBox .btn {
  padding: 0 20px 15px;
  font-size: 0.16rem;
  color: #0f5bf5;
}



